<template>
  <v-card class="rounded-xl mx-auto transparent" max-width="360" flat>
    <v-card-title class="font-weight-bold display-1 justify-center text-center">
      👶🏻 응애, 나 애기 🍼
    </v-card-title>
    <v-card-subtitle class="text-center title">
      난 태어난지 몇 개월 됐을까
    </v-card-subtitle>
    <v-card-text>
      <date-picker
        v-model="date"
        style="width: 100%"
        placeholder="내 생일이 언제더라,,"
        type="datetime"
        :show-time-panel="showTimePanel"
        @close="handleOpenChange"
      >
        <template v-slot:footer>
          <button class="mx-btn mx-btn-text" @click="toggleTimePanel">
            {{ showTimePanel ? "날짜 선택" : "시간 선택" }}
          </button>
        </template>
      </date-picker>
    </v-card-text>
    <v-card-actions class="px-4">
      <v-btn
        @click="calculateDate"
        class="rounded-lg body-1 font-weight-bold"
        depressed
        :disabled="!date"
        :ripple="false"
        block
        large
        color="primary"
      >
        계산
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";
import "vue2-datepicker/locale/ko";

export default {
  components: { DatePicker },
  data: () => ({
    date: "",
    showTimePanel: false,
  }),

  methods: {
    calculateDate() {
      if (!this.date) return;
      this.$gtag.event("calculate_date", { date: this.date });
      this.routerPush("Result", this.date.toString());
    },

    routerPush(routerName, id) {
      if (id === "") {
        return;
      }

      if (this.$router.currentRoute.params.id !== id) {
        this.$router
          .push({ name: routerName, params: { id: id } })
          .then(() => window.scrollTo(0, 0));
      }
    },

    toggleTimePanel() {
      this.showTimePanel = !this.showTimePanel;
    },

    handleOpenChange() {
      this.showTimePanel = false;
    },
  },
};
</script>

